$p-color:#0E8A00;
.text-nav{
    color: white;
}
.bg-nav{
    position: absolute;
    background-image: url("../images/index-1.jpg");
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 50%;
    margin-left: auto;
    height: 728px;
}
.bg{

    background-repeat: no-repeat;
    background-position: right;
    background-size: cover;
}

.product-img1{
    width: 540px;
    height:400px;
    background-image: url("../images/index-2.jpg");
}

.product-img2{
    width: 540px;
    height:400px;
    background-image: url("../images/index-3.jpg");
}
.product-img3{
    width: 540px;
    height:400px;
    background-image: url("../images/index-4.jpg");
}
.shop-btn{
    font-size: 1rem;
    line-height: 1.5rem;
    color: white;
    padding: 16px 48px 16px 48px;
}
.btn-success {
    color: #fff;
    background-color: #28a745;
    border-color: #28a745;
}
.btn-primary{
    background-color: #0E8A00;;
}
.flexc-center{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.product-show{
    .link{
        color:#0E8A00;
    }
    .link:hover{
        color:#28a745;
    }
}
.designer-part{
    .design-img{
        background-image: url("../images/index-5.jpg");
        width: 100%;
        height: 400px;
    }
    .design-solgan{
        font-size: 2rem;
        line-height: 3rem;
    }
    .text{
        font-size: 1rem;
        line-height: 1.5rem
    }
    .link{
        font-size: 0.875rem;
        line-height: 1rem;
        color:$p-color;
        cursor: pointer;
    }
    
    .name{
        font-size: 1.5rem;
        line-height: 1.5rem;
    }

}
.show-window{
    .card{
        border: 0px;
    }
    .card-img-top{
        border: 0px;
        width: 350px;
        height: 350px;
    }
    .card-body{
        padding: 0px;
        
    }
}

